import React from "react";
import "./Header.css";
import Button from '../main/navigation/Button';
import { Link } from "react-router-dom";
import dab from '../../images/dab.png';

const Header = () => (
  <header>
      <Link to="/" className="button-link">
        <h1>Maxcinema</h1>
      </Link>
    <img className='dab' src={dab} alt="dabbing weeb"/>
    <Link to="/" className="button-link"><Button>Main Page</Button></Link>
  </header>
)

export default Header;


