import React from 'react';
import "./SearchField.css";

const SearchField = ({ onSearchChange, onSearchField }) => {
    return (
        <form onSubmit={onSearchChange}>
            <input className="search-box" type="text" onChange={onSearchField}/>
            <input className="search-field" type="submit" value="Search" />
        </form>
    )
}

export default SearchField;


